    @font-face {
        font-family: arcana;
        src: url(./arcana.ttf);
    }

    body {
        font-family: arcana;
    }

    .flip-container {
        -webkit-perspective: 1000px;
                perspective: 1000px;
    }
    /* flip the pane when hovered */

    .flip-container.clicked .flipper,
    .flip-container.clicked .flipper {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }

    .flip-container,
    .front,
    .back {
        width: 235px;
        height: 415px;
    }
    /* flip speed goes here */

    .flipper {
        -webkit-transition: 0.6s;
        -o-transition: 0.6s;
        transition: 0.6s;
        -webkit-transform-style: preserve-3d;
                transform-style: preserve-3d;
        position: relative;
    }
    /* hide back of pane during swap */

    .front,
    .back {
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
    }
    /* front pane, placed above back */

    .front {
        z-index: 2;
        /* for firefox 31 */
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    /* back, initially hidden pane */

    .back {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
        padding: 10px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        background-color: whitesmoke;
        position: relative;
    }

    h2 {
        text-align: center;
    }

    .card {
        width: 235px;
        height: 415px;
        border-radius: 3%;
        overflow: hidden;
        -webkit-box-shadow: 1px 1px 1px 2px rgba(0, 0, 0, 0.3);
                box-shadow: 1px 1px 1px 2px rgba(0, 0, 0, 0.3);
        display: inline-block;
    }

    .card img {
        width: 100%;
        height: 100%;
    }

    .description {
        position: absolute;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        top: 90%;
        left: 0;
        width: 100%;
        height: 100%;
        -webkit-transition: top 0.5s, opacity 0.6s, border 0.1s;
        -o-transition: top 0.5s, opacity 0.6s, border 0.1s;
        transition: top 0.5s, opacity 0.6s, border 0.1s;
        padding: 30% 15px 0;
        /* background: linear-gradient(rgba(255, 255, 255, 0), whitesmoke 20%) */
        background: rgba(175, 156, 114, 0.9);
        font-size: 18px;
        opacity: 0;
        border: 12px rgba(0, 0, 0, 0);
        overflow-y: scroll;
    }

    ::-webkit-scrollbar {
        display: none;
    }

    .description:hover {
        top: 0%;
        opacity: 1;
        border: 12px solid whitesmoke;
        border-radius: 1%;
    }


